






























import Vue from 'vue'

type DataType = {
  coin: number;
  buyNumber: number;
};

export default Vue.extend({
  data(): DataType {
    return {
      coin: 0,
      buyNumber: 1
    }
  },
  methods: {
    startReceiving() {
      (window as any).webkit.messageHandlers.FCM.postMessage(`START_RECIEVING`);
    },
  }
})
